iene implicaciones tributarias. En el contexto de la contratación pública, estas relaciones son especialmente relevantes debido a la necesidad de asegurar que los recursos públicos sean manejados de manera eficiente y conforme a las leyes fiscales.")]),_vm._m(6),_c('p',{staticClass:"mb-4"},[_vm._v("Conozcamos algunos de ellos:")]),_c('div',{staticClass:"row mb-4"},[_vm._m(7),_c('div',{staticClass:"col-12 col-lg-8",attrs:{"data-aos":"fade-left"}},[_c('AcordionA',{attrs:{"tipo":"b"}},[_c('div',{staticClass:"div",attrs:{"titulo":"Retenciones tributarias en la contratación pública"}},[_c('p',[_vm._v("Uno de los principales vínculos entre el Derecho tributario y la contratación, es la obligación de las entidades públicas de actuar como agentes de retención. Esto significa que, al realizar pagos a contratistas, las entidades deben retener ciertos impuestos, como el impuesto sobre la renta o el impuesto al valor agregado (IVA), y transferirlos a la administración tributaria. Esta medida asegura que los contratistas cumplan con sus obligaciones fiscales, evitando posibles evasiones."),_c('br'),_vm._v("Por ejemplo, en Colombia, la retención en la fuente es un mecanismo obligatorio en la contratación pública. La entidad contratante retiene un porcentaje del pago al contratista y lo reporta a la Dirección de Impuestos y Aduanas Nacionales (DIAN). Este procedimiento es clave para garantizar que el Estado recaude los impuestos correspondientes de manera eficiente.")])]),_c('div',{staticClass:"div",attrs:{"titulo":"Obligaciones tributarias de los contratistas"}},[_c('p',[_vm._v("Los contratistas, ya sean personas naturales o jurídicas, están obligados a cumplir con las normativas tributarias relacionadas con sus ingresos derivados de contratos públicos o privados. Esto incluye:"),_c('br'),_vm._v(" o Declarar y pagar impuestos sobre la renta, por los ingresos obtenidos."),_c('br'),_vm._v(" o Liquidar y pagar el IVA si están gravados."),_c('br'),_vm._v(" o Registrar y cumplir con e